body{
  background-color: #A7F3D0;
}

.App{
  background-color: #34D399;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: #08a772;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius:10px;
  color:white;
}

#to-do-form input::placeholder{
  color: rgb(255,255,255,0.5);
}

#to-do-form input:focus, #to-do-form button:focus{
  outline: none;
}

#to-do-form button{
  background-color: #065F46;
  color: white;
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
  font-weight: bolder;
}
